*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background-color: aliceblue;
    color: #222;
}

.booklist{
    width: 90vw;
    max-width: 1170px;
    margin:2rem auto;
    display: grid;
    gap: 3rem;
}

.book img{
    width: 100%;
    object-fit: cover;
    border-radius: 1rem ;
}

.book h2{
    color: red;
    font-size: 0.75rem;
    margin-top: 0.5rem;
    letter-spacing: 2px;
}

@media screen and (min-width: 768px){
    .booklist{
        grid-template-columns: repeat(3, 1fr);
    }
}

.book{
    background-color: #fff;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    position: relative;
}

.book img{
    width: 100%;
    object-fit: cover;
}

.book h2{
    margin-top: 1rem;
    font-size: 1rem;
}

.button1 ,.button2{
    background-color: rgb(74, 169, 183);
    width: 7rem;
    /* height: 5rem; */
    /* height: 50px; */
    margin-right: 2rem;
}

.number{
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 1rem 0 2rem 0;
    background-color: red;
    width: 3rem;
    height: 2rem;
    color: #fff;
    align-items: center;
    justify-content: center;
}

.title{
    text-align: center;
    font-size: 2rem;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    color: rgb(255, 157, 0);
}
